/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/

/* Links */

a {
	color: $color__link;

	&:visited {
		color: $color__link-visited;
	}
	&:hover,
	&:focus,
	&:active {
		color: $color__link-hover;
	}
	&:focus {
		outline: thin dotted;
	}
	&:hover,
	&:active {
		outline: 0;
	}
}

/* Menus */

.main-navigation {
	clear: both;
	display: block;
	float: left;
	width: 100%;

	ul {
		display: none;
		list-style: none;
		margin: 0;
		padding-left: 0;

		ul {
			box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
			float: left;
			position: absolute;
			top: $base__space-unit;
			left: -999rem;
			z-index: 99999;

			ul {
				left: -999rem;
				top: 0;
			}

			li {
				&:hover > ul,
				&.focus > ul {
					left: 100%;
				}
			}

			a {
				width: 200px;
			}

			:hover > a,
			.focus > a {
			}

			a:hover,
			a.focus {
			}
		}

		li:hover > ul,
		li.focus > ul {
			left: auto;
		}
	}
	li {
		float: left;
		position: relative;

		&:hover > a,
		&.focus > a {
		}
	}
	a {
		display: block;
		text-decoration: none;
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a {
	}
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

@media screen and (min-width: 768px) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: block;
	}
}

.comment-navigation,
.posts-navigation,
.post-navigation {

	.site-main & {
		margin: 0 0 $base__space-unit;
		overflow: hidden;
	}

	.nav-previous {
		float: left;
		width: 50%;
	}

	.nav-next {
		float: right;
		text-align: right;
		width: 50%;
	}
}

/* My CSS */

.container {
  display: flex;
  justify-content: space-between;
  padding: 10px 0 0 20px;
}
.main-navigation {
	display: flex;
	justify-content: space-between;
	a {
		margin: 0.8rem;
	}
	.fa {
		padding: 25px 60px;
		padding-left: 10px;
	}
	.white-tent {
		width: 4.5rem;
		padding-bottom: 10px;
		img {
			padding-top: 5px;
		}
	}
}
fieldset {
  border: none;
  #search-submit {
    border: none;
    background-color: transparent;
		box-shadow: none;
		margin-top: 0px;
    .fa.fa-search {
      color: white;
      text-shadow: 1px 1px 2px #000;
    }
  }
}
.menu-main-menu-container {
	padding-top: 12px;
	margin-left: 44rem;
	font-size: .9rem;
}
.site-header {
	text-transform: uppercase;
	-webkit-text-fill-color: #248a83;
	background-color: hsla(0,0%,100%,.85);
	width: 100%;
	position: fixed;
	z-index: 3;
	border: 1px solid #e1e1e1;
	.container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: -20px;
	}
	.site-branding {
		height: 33px;
		width: 50px;
	}
}
.home .entry-title a {
	display: none;
}